#products-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  .contenar{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  .product {
width: 323px;
height: 430px;
padding: 10px;
margin: 10px;

}
svg{
height: 30px;
display: flex;
z-index: 2;
float: right;
cursor: pointer;
/* fill: red; */
 }
 path:hover{
  stroke: #ff0000;
 }
 .active{
  stroke: #ff0000;
 }
 .inactive{
  stroke: #000000;
 }
 
  .product:hover{
    box-shadow: 0 3px 10px rgb(0 0 0 / 0.5);
    border-radius: 16px;

  }
  .Lin{
    border: 1px sold black;
    width: 200px;
    height: 45px;
    background-color: rgba(151, 151, 151, 0.253);
    padding: 5px;
    text-align: center;
    border-radius: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
  }
  .product img {
    max-width: 100%;
    border-radius: 16px;
    height: 270px;
  }
  
  .cardbody{
    display: flex;
    margin-top: 25px;
  }
  .product h2 {
    
width: 320px;
height: 20px;
font-family: 'Outfit';
font-style: normal;
font-weight: bolder;
font-size: 18px;
overflow: hidden;
color: black;

  }
  
  .product p {
max-width: 310px;
    height: 18px;
font-family: 'Outfit';
font-style: normal;
font-weight: 500;
font-size: 16px;
line-height: 20px;
overflow: hidden;

/* identical to box height */


color: rgba(0, 0, 0, 0.5);
  }
  
  h3 ,.H2{
    text-align: center;
    margin-top: 30px;
    padding: 30px;
  }
  button{
    border: none;
    background: none;
    position: absolute;
    float: right;
  }